body {
  background-color: lightgreen;
  font-family: 'VT323', monospace;
  color: white;
}

.topdiv {
  display: flex;
  justify-content: space-between;
}

#machinetitle {
  margin-right: 110px; 
  font-size: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#motherdiv {
  padding-top: 75px;
}

.blockdiv {
  position: relative;
  flex-direction: column;
  margin: 0 auto;
  width: 80%;
  height: 500px;
  background-color: blue;
}

.blockdivcontent h2 {
  padding-top: 50px;
  padding-left: 85px;
  font-size: 35px;
}

.tempobutton {
  padding-left: 50px;
  display: inline-block;
  font-size: 40px;
  background-color: grey;
  margin-left: 30px;
  margin-top: -10px;
  padding-right: 50px;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera, and Firefox */
}

.tempodisplay {
  margin-left: 85px;
  font-size: 30px;
}


.buttonsdiv {
  background-color: white;
  display: flex;
  justify-content: space-around;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 30%;
}

.displayButtons {
  display: flex;
  justify-content: space-around;
}

.drumSound {
  background-color: grey;
  font-size: 30px;
  width: 50px;
  text-align: center;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera, and Firefox */
}

.hlDrumSound {
  background-color: red;
  font-size: 30px;
  width: 50px;
  text-align: center;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera, and Firefox */

}

.trigButton {
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  background-color: black;
  height: 30%;
  width: 30px;
  flex-grow: none;
}

.redClass {
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  background-color: red;
  height: 30%;
  width: 30px;
  flex-grow: none;
}

.highlighted {
  border-bottom: solid 10px red;
}